/* src/App.css */

@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');

body {
  display: flex;
  justify-content: center;
  background-color: #34373B;
  font-family: "Montserrat", serif;
}

.App {
  text-align: center;
  background-color: #34373B;
  justify-content: center;
  display: flex;
  max-width: 1300px;
  width: 100vw;
  flex-direction: column;
  align-items: center;
}

.App-header {
  width: 100%;

}
.App-body {
  width: 100%;
  padding: 20px;
}
h2 {
  margin-bottom: 20px;
  color: #333;
}

.user-stats {
  max-width: 130px;
}

.etf-row {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-bottom: -20px; /* Reduced margin-bottom to close the gap */
}

::-webkit-inner-spin-button {
  appearance:none;
}

@keyframes blipGreen {
  0% { color: rgb(72, 129, 129); }
  50% { background-color: transparent; }
}

@keyframes blipRed {
  0% { color: rgb(150, 60, 60); }
  50% { background-color: transparent; }
}

.blipGreen {
  animation: blipGreen 0.9s ease;
}

.blipRed {
  animation: blipRed 0.9s ease;
}

/* styles.css */
@keyframes slideOut {
  0% {
    transform: translateX(0);
    opacity: 1;
  }
  100% {
    transform: translateX(-100%);
    opacity: 0;
  }
}

.trader-slide-out {
  animation: slideOut 2s forwards;
 
}

